import React from "react";
import { Link, useHistory } from "react-router-dom";
import * as Auth from "../utils/Auth.js";

function Register({ onSubmitClick, handleRegister }) {
  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");
  const history = useHistory();
  const escape = require("escape-html");

  function handleChangeEmail(e) {
    setEmail(e.target.value);
  }

  function handleChangePassword(e) {
    setPassword(e.target.value);
  }

  function submitClick() {
    setTimeout(onSubmitClick, 500);
  }

  function handleSubmit(e) {
    e.preventDefault();
    Auth.register(escape(email), escape(password))
      .then((res) => {
        if (res) {
          handleRegister();
          history.push("/sign-in");
        } else {
          throw new Error("–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫!");
        }
      })
      .catch((err) => {
        console.log(`–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è: ${err}`);
      });
  }

  return (
    <div className="login">
      <h3 className="login__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h3>
      <form onSubmit={handleSubmit} className="login__form">
        <fieldset className="login__form-set">
          <input
            type="email"
            name="email"
            value={email}
            placeholder="Email"
            onChange={handleChangeEmail}
            className="login__form-text login__form-text_type_title"
            required
          />
          <input
            type="password"
            name="password"
            value={password}
            placeholder="–ü–∞—Ä–æ–ª—å"
            onChange={handleChangePassword}
            className="login__form-text login__form-text_type_password"
            required
          />
          <input
            type="submit"
            onClick={submitClick}
            value="–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"
            className="login__form-submit"
          />
        </fieldset>
      </form>
      <div className="login__signup">
        <p className="login__signup-text">–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã?</p>
        <Link to="/sign-in" className="login__signup-link">
          –í–æ–π—Ç–∏
        </Link>
      </div>
    </div>
  );
}

export default Register;
