import React from "react";
import PopupWithForm from "./PopupWithForm";

function EditAvatarPopup({ isOpen, onClose, onUpdateAvatar }) {
  const avatarRef = React.useRef();

  function handleSubmit(e) {
    e.preventDefault();

    onUpdateAvatar({
      avatar: avatarRef.current.value,
    });
  }

  return (
    <PopupWithForm
      title="–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"
      name="edit-avatar"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
    >
      <input
        type="url"
        name="link"
        placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
        ref={avatarRef}
        className="popup__form-text popup__form-text_type_link"
        id="url-input"
        autoComplete="off"
        required
      />
      <span className="popup__form-text-error" id="url-input-error"></span>
      <input
        type="submit"
        value="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
        className="popup__form-submit popup__form-submit_type_edit-avatar"
      />
    </PopupWithForm>
  );
}

export default EditAvatarPopup;
